import { useContext } from "react";
import { useCssStateManager } from "../../hooks/use_css_state_manager";
import { LayoutStateContext } from "../../contexts/layout_state";

export type DeviceIndependentBodyProps = {
    desktopWidth?: number;
    children: any;
};

export default function DeviceIndependentBody({children,desktopWidth = 1000}:DeviceIndependentBodyProps)
{
    const cssStateManager = useCssStateManager(["device-independent-body"]);
    const { mobileDetect: { widthCutoffStyleSection } } = useContext(LayoutStateContext);
    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <div className="inner">
                    {children}
                </div>
            </div>
            <style jsx>{`
            .device-independent-body {
                width: 100%;
                display: flex;
                justify-content: center;
            }

            .inner {
                max-width: ${desktopWidth}rem;
                min-width: ${desktopWidth}rem;
            }

            ${widthCutoffStyleSection(`
                .device-independent-body .inner {
                    max-width: none;
                    min-width: 0rem;
                    width: 100%;
                }
            `)}
            `}</style>
        </>
    );
}