import { useContext } from "react";
import { useCssStateManager } from "../../../hooks/use_css_state_manager";
import { InlineButton, styledButton } from "../../common/button";
import { LayoutStateContext } from "../../../contexts/layout_state";
import IconButton from "../../common/icon-button";
import { useCustomRouter } from "../../../hooks/use_custom_router";

export type AccountManageAccountSectionProps = {
    caption: string;
    value: string;
    path?: string;
};

const Button = styledButton({display: "inline-block",padding: "11rem 28rem"});

export default function AccountManageAccountSection({caption,value,path}:AccountManageAccountSectionProps)
{
    const { linearTopWorkingIndicatorRunning,mobileDetect: { widthCutoffStyleSection } } = useContext(LayoutStateContext);
    const cssStateManager = useCssStateManager(["manage-account-section"]);
    cssStateManager.useProperty(!value,"value-none");

    const router = useCustomRouter();

    const handleEditClick = () => {
        if (path) {
            router.push(path);
        }
    };

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <div className="info">
                    <div className="caption">{caption}</div>
                    <div className="value">{value?value:"(none)"}</div>
                </div>
                <div className="edit-button">
                    <Button caption="Edit" onClick={handleEditClick} />
                </div>
                <div className="mobile-edit-button">
                    <IconButton iconName="edit" extraPaddingHori={4} onClick={handleEditClick} />
                </div>
            </div>
            <style jsx>{`
            .manage-account-section {
                --border-color: #ccc;
                border-left: 1rem solid var(--border-color);
                border-right: 1rem solid var(--border-color);
                border-bottom: 1rem solid var(--border-color);
                padding: 20rem;
                display: flex;
                justify-content: space-between;
                position: relative;
            }
            .info {
                font-size: 15rem;
                overflow: hidden;
            }
            .caption {
                font-weight: 600;
                margin-bottom: 10rem;
            }
            .value {
                overflow: hidden;
                text-overflow: ellipsis;
                color: #777;
            }
            .value-none .value {
                color: #aaa;
            }
            .edit-button {
                display: flex;
                flex-direction: column;
                justify-content: center;
                padding-left: 20rem;
            }
            .mobile-edit-button {
                display: none;
                position: absolute;
                right: 7rem;
                top: 7rem;
            }

            ${widthCutoffStyleSection(`
                .manage-account-section .info {
                    font-size: 13rem;
                }

                .manage-account-section .edit-button {
                    display: none;
                }

                .manage-account-section .mobile-edit-button {
                    display: block;
                }
            `)}
            `}</style>
        </>
    );
}