import { CSSProperties } from "react";
import { useCssStateManager } from "../../hooks/use_css_state_manager";
import { EventPane } from "../core/event_pane";

export type IconButtonProps = {
    iconName: string;
    fontSize?: number;
    padding?: number;
    extraPaddingHori?: number;
    extraPaddingVerti?: number;
    color?: string;
    hoverColor?: string;
    onClick?: () => void;
    disabled?: boolean;
};

export default function IconButton({iconName,fontSize = 20,color = "#666",hoverColor = "#222",padding = 10,extraPaddingHori = 0,extraPaddingVerti = 0,onClick = () => {},disabled}:IconButtonProps) {
    const cssStateManager = useCssStateManager(["icon-button"]);
    cssStateManager.useProperty(disabled,"disabled");
    const innerStyles: CSSProperties = {
        padding: `${padding+extraPaddingVerti}rem ${padding+extraPaddingHori}rem`
    };
    const iStyles: CSSProperties = {
        fontSize: `${fontSize}rem`
    };

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <div className="inner" style={innerStyles}>
                    <i className={`icon-${iconName}`} style={iStyles} />
                </div>
                <EventPane onClick={onClick} disabled={disabled} />
            </div>
            <style jsx>{`
            .icon-button {
                display: inline-block;
                position: relative;
                transition: background-color 0.3s;
            }

            .disabled {
                opacity: 0.7;
            }

            .inner {
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
            }

            i {
                display: inline-block;
                color: ${color};
                transition: color 0.3s;
            }

            .icon-button.touchdown {
                background-color: rgba(0,0,0,0.08);
            }

            .icon-button.hover i {
                color: ${hoverColor};
            }
            `}</style>
        </>
    );
}