import { useCssStateManager } from "../../hooks/use_css_state_manager";
import { useAccountBarParentNav } from "../account-bar";
import Desktop from "../common/desktop";
import Mobile from "../common/mobile";
import MobileTitleWithBack from "../common/mobile-title-with-back";

export type AccountTitleProps = {
    caption: string;
    backPath?: string;
    backPathCaption?: string;
};

export default function AccountTitle({caption,backPath,backPathCaption}:AccountTitleProps)
{
    const cssStateManager = useCssStateManager(["account-title"]);
    if (backPath && backPathCaption) {
        useAccountBarParentNav(backPathCaption,backPath);
    }
    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                <Desktop>
                    <div className="title">{caption}</div>
                </Desktop>
                <Mobile>
                    {backPath?(
                        <MobileTitleWithBack caption={caption} backPath={backPath} />
                    ):(
                        <div className="title">{caption}</div>
                    )}                    
                </Mobile>
            </div>
            <style jsx>{`
            .account-title {
                margin-bottom: 25rem;
            }
            .title {
                font-size: 22rem;
                font-weight: 600;                
                text-align: center;
            }
            `}</style>
        </>
    );
}