import { useCssStateManager } from "../../hooks/use_css_state_manager";
import { useCustomRouter } from "../../hooks/use_custom_router";
import { EventPane } from "../core/event_pane";

export type MobileTitleWithBackProps = {
    caption: string;
    backPath: string;
};

export default function MobileTitleWithBack({caption,backPath}:MobileTitleWithBackProps) {
    const cssStateManager = useCssStateManager(["mobile-title-with-back"]);

    const router = useCustomRouter();

    const handleBackClick = () => {
        router.push(backPath);
    };

    return (
        <>
            <div className={cssStateManager.getClassNames()}>
                {caption}
                <div className="back-button">
                    <i className="icon-navbar-left-arrow" />
                    <EventPane onClick={handleBackClick} />
                </div>
            </div>
            <style jsx>{`
            .mobile-title-with-back {
                font-size: 22rem;
                font-weight: 600;
                text-align: center;
                position: relative;
                width: 100%;
            }

            .back-button {
                --size: 40rem;
                position: absolute;
                left: -10rem;
                top: -5rem;
                width: var(--size);
                height: var(--size);                    
                display: flex;
                justify-content: center;
                align-items: center;                    
            }

            .back-button i {
                display: block;
                font-size: 20rem;
                color: #777;
            }

            .back-button.touchdown {
                background-color: rgba(0,0,0,0.07);
            }
            `}</style>
        </>
    );
}
